@import '../../styles/breakpoints';

.sb-treebrowser {
  --sb-collapsible-panel-inner-padding: 0;
  --sb-collapsible-panel-title-border: solid 1px
    var(--sbt-theme-tertiary-color-20);
  --sb-collapsible-panel-rotation: 180deg;
}

.sb-treebrowser__tree-header {
  border-bottom: solid 1px var(--sbt-theme-tertiary-color-20);
  padding-left: var(--sbt-spacing-40);
  padding-right: var(--sbt-spacing-40);
  @include window-width-max-slim {
    padding-right: var(--sbt-spacing-40);
  }
}

.sb-treebrowser__tree-data {
  padding: 0;
}

.sb-treebrowser__option {
  padding: var(--sbt-spacing-10);
}
