@mixin center-element {
  align-items: center;
  display: flex;
}

@mixin hover-element {
  background-color: var(--sbt-theme-color-30);
  border-radius: var(--sb-button-border-radius);
  cursor: pointer;
}

@mixin text-overlay {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.sb-treebrowser__tree-header {
  cursor: pointer;
  margin-bottom: var(--sbt-spacing-10);

  &-selected,
  &:hover:not(.sb-treebrowser__tree-header-disabled) {
    @include hover-element;

    .sb-treebrowser__header-name {
      --sb-typography-color: var(--sb-badge-color, var(--sbt-theme-color-90));
    }
  }

  .sb-treebrowser__header {
    display: flex;
  }
}

.sb-treebrowser__tree-header-disabled {
  cursor: default;
}

.sb-treebrowser__header {
  @include center-element;
  display: grid;
  flex-direction: row;
  grid-template-columns: 1fr auto;
  justify-content: space-between;
  width: 100%;

  &-name {
    @include text-overlay;
  }

  &-icon {
    align-self: center;
  }

  &-list {
    --sb-icon__color: var(--sbt-theme-tertiary-color-40);
    @include center-element;
    font-size: var(--sbt-font-size-35);
    padding-right: var(--sbt-spacing-70);
  }

  &-badge {
    margin-left: var(--sbt-spacing-20);
  }

  &-status {
    @include text-overlay;
    flex-shrink: 0;
    max-width: 100px;
  }

  &-center {
    @include center-element;
    border-bottom: var(--sb-collapsible-panel-title-border, none);
    margin-left: var(--sbt-spacing-30);
    padding: 0 1rem;

    &-selected,
    &:hover {
      @include hover-element;
    }
  }

  &-interactive {
    flex-grow: 1;
    overflow: hidden;
    padding: var(--sbt-spacing-20);
    padding-left: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.sb-treebrowser__header-name {
  @include center-element;
  font-weight: var(--sbt-font-weight-60);
}

.sb-treebrowser__header-name-disabled {
  color: var(--sbt-theme-tertiary-color-50);
  cursor: not-allowed;
}

.sb-treebrowser__header-expand.sb-treebrowser__header-expand {
  --sb-button-color: var(--sbt-theme-tertiary-color-90);
  --sb-button-icon-only-width: 30px;
  margin: 0;
  margin-right: var(--sbt-spacing-10);
  -moz-transition: all 0.3s linear;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

.sb-treebrowser__header-down {
  -moz-transform: rotate(-180deg);
  -webkit-transform: rotate(-180deg);
  transform: rotate(-180deg);
}

.sb-treebrowser__header-trigger {
  @include text-overlay;
  cursor: pointer;
}

.sb-treebrowser__header-trigger-disabled {
  cursor: not-allowed;
}

.sb-treebrowser__header-name-new {
  cursor: not-allowed;
}

.sb-treebrowser__header-actions {
  align-items: center;
  display: inline-flex;
}

.sb-treebrowser__header-action:not(:last-child) {
  margin-right: var(--sbt-spacing-20);
}

.sb-treebrowser__header-add {
  margin: 0;
  padding: var(--sbt-spacing-10);
}

.sb-treebrowser__node-new {
  --sb-typography-color: var(--sbt-theme-tertiary-color-40);
  --sb-spinner-color: var(--sbt-theme-tertiary-color-40);
}
