.sb-menu-list__check {
  color: var(--sbt-theme-color-70);
  padding: var(--sbt-spacing-10) 0;
}

.sb-menu-list__check-unselected {
  visibility: hidden;
}

.sb-menu-list__label-selected {
  font-weight: var(--sbt-font-weight-60);
}

.sb-menu-list__option {
  display: flex;
  gap: var(--sbt-spacing-20);
}

.sb-menu-list {
  width: 100%;

  &,
  .sb-select,
  .sb-select__menu,
  .sb-select__menu-list {
    height: 100%;
    max-height: initial;
    top: 0;
  }

  .sb-select__control {
    display: none;
  }

  .sb-select__menu {
    border-radius: 0;
    margin: 0;
    position: relative;
  }

  /* stylelint-disable-next-line selector-max-compound-selectors */
  .sb-select .sb-select__option {
    padding: var(--sbt-spacing-30) var(--sbt-spacing-40);
  }

  .sb-select__option--is-selected.sb-select__option--is-selected:not(:hover) {
    background-color: inherit;
    color: inherit;
  }

  .sb-select__group {
    padding: 0 var(--sbt-spacing-20);
  }
}

.sb-menu-list__flat {
  --sb-select-menu-elevation: none;
  --sb-select-menu-background: transparent;
}
