@import '../../styles/mixins';

.sb-treebrowser__leaf {
  --sb-icon__color: var(--sbt-theme-tertiary-color-40);
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  margin: var(--sbt-spacing-10) 0;
  padding: var(--sbt-spacing-20);
  padding-left: var(--sbt-spacing-100);
  transition: 0.2s ease;

  &-active {
    --sb-typography-color: var(--sb-badge-color, var(--sbt-theme-color-90));
    background-color: var(--sbt-theme-color-30);
    border-radius: var(--sb-button-border-radius);
  }

  &-left {
    @include text-ellipsis;
    column-gap: var(--sbt-spacing-30);
    display: flex;
  }

  &-badge {
    display: inline-block;
    text-transform: capitalize;
  }

  &-name {
    @include text-ellipsis;
  }

  &:hover:not(.sb-treebrowser__leaf-disabled) {
    --sb-typography-color: var(--sb-badge-color, var(--sbt-theme-color-90));
    background-color: var(--sbt-theme-color-30);
    border-radius: var(--sb-button-border-radius);
  }
}

.sb-treebrowser__leaf-disabled {
  cursor: not-allowed;

  .sb-treebrowser__leaf-left {
    --sb-typography-color: var(--sbt-theme-tertiary-color-50);
    cursor: not-allowed;
  }

  .sb-treebrowser__leaf-badge {
    cursor: not-allowed;
  }
}

.sb-treebrowser__new {
  --sb-typography-color: var(--sbt-theme-tertiary-color-40);
  --sb-spinner-color: var(--sbt-theme-tertiary-color-40);
  cursor: not-allowed;
}
